@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
* {
  box-sizing: border-box;
}

:root {
  --color-background: #0a0a0a;
  --color-surface: #141414;
  --color-text: #fff;
  --color-text-secondary: #fff9;
