  --color-button-disabled: #ffffff08;
  --shadow-sm: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
  --radius-sm: 3px;
  --radius-md: 6px;
  --radius-lg: 8px;
