  --space-lg: 16px;
  --space-xl: 20px;
  --space-2xl: 24px;
}

html {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  letter-spacing: -.011em;
  background: var(--color-background);
  min-height: 100%;
  color: var(--color-text);
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  font-weight: 400;
  display: flex;
  overflow-x: hidden;
}

.ad-header {
  background: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  text-align: center;
  padding: var(--space-sm);
  flex-shrink: 0;
  width: 100%;
  font-style: italic;
}

.main-header {
  background: var(--color-surface);
  color: var(--color-text);
  text-align: center;
  padding: var(--space-2xl);
  border-bottom: 1px solid var(--color-border);
  flex-shrink: 0;
  width: 100%;
}

.main-header h1 {
  letter-spacing: -.02em;
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

main {
  padding: var(--space-2xl);
  gap: var(--space-2xl);
  background: var(--color-background);
  box-sizing: border-box;
  flex-direction: column;
  flex: 1 0 auto;
  width: 100%;
  max-width: 1400px;
  display: flex;
}

.chart-container {
  width: 100%;
  position: relative;
}

#race {
  border: 1px solid var(--color-border);
  background-color: var(--color-surface);
  border-radius: var(--radius-lg);
  width: 100%;
  min-height: 200px;
}

#race text.dateCounterText {
  font-size: 40px;
}

#race text.label.outside-bars, #race .axis .tick text {
  font-size: 16px;
}

#race text.valueLabel {
  font-size: 12px;
}

.chart-overlay {
  border-radius: var(--radius-lg);
  opacity: 0;
  pointer-events: none;
  background: #0a0a0acc;
  justify-content: center;
  align-items: center;
  width: calc(100% + 4px);
  transition: opacity .2s;
  display: flex;
  position: absolute;
  inset: 0;
}

.chart-overlay.visible {
  opacity: 1;
  pointer-events: all;
}

.loader {
  border: 3px solid var(--color-border);
  border-top-color: var(--color-text);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
  display: none;
}

.chart-overlay.loading .loader {
  display: block;
}

.chart-overlay.loading .play-button {
  display: none;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.play-button {
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  cursor: pointer;
  width: 60px;
  height: 60px;
  color: var(--color-text);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.play-button:hover {
  background: var(--color-hover);
  transform: scale(1.05);
}

.play-button:active {
  transform: scale(.95);
}

.play-icon {
  margin-left: 4px;
  font-size: 24px;
}

footer {
  color: #fff;
  background-color: #1a1a1a;
  width: 100%;
  padding: 2rem 0;
}

.footer-content {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
}

.footer-section {
  flex: 1;
  min-width: 250px;
  max-width: 400px;
}

.footer-section h3 {
  color: #fff;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.footer-section p {
  color: #ccc;
  line-height: 1.5;
}

.footer-link {
  color: #ccc;
  align-items: center;
  gap: .5rem;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.footer-link:hover {
  color: #fff;
}

.icon {
  fill: currentColor;
  width: 24px;
  height: 24px;
}

@media (width <= 768px) {
  .footer-content {
    text-align: center;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .footer-section {
    min-width: 200px;
  }

  .footer-link {
    justify-content: center;
  }
}

.ad-below-chart {
  background: var(--color-surface);
  padding: var(--space-lg);
  text-align: center;
  border-top: 1px solid var(--color-border);
  flex-shrink: 0;
  width: 100%;
  font-style: italic;
}

.ad-below-chart p {
  margin: 0;
}

@media (width <= 768px) {
  body {
    font-size: 14px;
  }

  main {
    padding: var(--space-lg);
    gap: var(--space-lg);
  }

  .main-header h1 {
    font-size: 1.2rem;
  }

  .coin-selector-container, .available-coins-container {
    padding: var(--space-lg);
  }

  .search-container input {
    width: 150px;
  }

  .available-coins-header {
    align-items: stretch;
    gap: var(--space-md);
    flex-direction: column;
  }

  .coin-selector, .available-coins {
    max-height: 150px;
  }
}

.coin-selector-container, .available-coins-container {
  padding: var(--space-xl);
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-lg);
  box-sizing: border-box;
  width: 100%;
  box-shadow: var(--shadow-sm);
  margin: 0;
}

.coin-selector-header, .available-coins-header {
  margin-bottom: var(--space-lg);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.coin-selector-header h3, .available-coins-header h3, .coin-selector-container h3, .available-coins-container h3 {
  color: var(--color-text);
  letter-spacing: -.01em;
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.refresh-coins {
  align-items: center;
  gap: var(--space-xs);
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  color: var(--color-text-secondary);
  padding: var(--space-xs) var(--space-sm);
  cursor: pointer;
  font-family: inherit;
  font-size: .9rem;
  transition: all .2s;
  display: inline-flex;
}

.refresh-coins:hover:not(.disabled) {
  background: var(--color-hover);
  color: var(--color-text);
  border-color: var(--color-text-secondary);
}

.refresh-coins:active:not(.disabled) {
  transform: scale(.98);
}

.refresh-coins .refresh-icon {
  font-size: 1.1em;
  transition: transform .3s;
  display: inline-block;
}

.refresh-coins:hover:not(.disabled) .refresh-icon {
  transform: rotate(180deg);
}

.refresh-coins.disabled {
  background: var(--color-button-disabled);
  color: var(--color-text-disabled);
  cursor: default;
  opacity: .5;
  pointer-events: none;
  border-color: #0000;
}

.coin-selector, .available-coins {
  gap: var(--space-md);
  max-height: 200px;
  padding: var(--space-xs);
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: var(--color-hover) transparent;
  flex-wrap: wrap;
  width: 100%;
  display: flex;
  overflow-y: auto;
}

.coin-selector::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.available-coins::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.coin-selector::-webkit-scrollbar-track {
  background: none;
}

.available-coins::-webkit-scrollbar-track {
  background: none;
}

.coin-selector::-webkit-scrollbar-thumb {
  background: var(--color-hover);
  border-radius: var(--radius-sm);
}

.available-coins::-webkit-scrollbar-thumb {
  background: var(--color-hover);
  border-radius: var(--radius-sm);
}

.coin-selector::-webkit-scrollbar-thumb:hover {
  background: var(--color-text-secondary);
}

.available-coins::-webkit-scrollbar-thumb:hover {
  background: var(--color-text-secondary);
}

* {
  scrollbar-width: thin;
  scrollbar-color: #444 #1e1e1e;
}

.coin-item, .available-coin-item {
  background: var(--color-surface-hover);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  cursor: pointer;
  backdrop-filter: blur(8px);
  align-items: center;
  transition: all .2s;
  display: flex;
}

.coin-item:hover, .available-coin-item:hover {
  background: var(--color-hover);
  transform: translateY(-1px);
}

.coin-item img, .available-coin-item img {
  width: 24px;
  height: 24px;
  margin-right: var(--space-sm);
  border-radius: 50%;
}

.remove-coin, .add-coin {
  color: var(--color-text-secondary);
  cursor: pointer;
  padding: 0 0 0 var(--space-sm);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 20px;
  transition: all .2s;
  display: flex;
}

.remove-coin:hover {
  color: var(--color-accent-danger);
}

.remove-coin:focus, .add-coin:focus {
